:root {
  --bg-color: linear-gradient(to right, #434343 0%, black 100%);
  --txt-color: #11c011;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-image: var(--bg-color);
  color: var(--txt-color);
  font-family: monospace;
}

h1 {
  text-shadow: 0 1px #434343, 0 1px #ccc, 0 2px #434343, 0 2px #ccc, 0 3px #434343, 0 3px #ccc, 0 5px #434343, 0 5px #ccc, 0 6px #434343, 0 6px #ccc, 0 8px #434343, 0 8px #ccc, 0 9px 15px #00000080;
  font-size: 4rem;
}

.flex-container {
  text-align: center;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  display: flex;
}

.center {
  justify-content: center;
  display: flex;
}

.place-content-center {
  max-width: 1000px;
  height: 100vh;
}

.flex-container * {
  width: 98%;
  color: var(--txt-color);
  margin: .4rem 0;
  padding: .2rem;
}

.form-container {
  flex-direction: column;
  display: flex;
}

.form-container input, .form-container button, .write-post-section button {
  background-color: #818181;
}

.form-container input::placeholder, .write-post-section button {
  color: var(--txt-color);
  font-variant: small-caps;
}

a:hover {
  color: var(--txt-color);
}

header {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding: .5rem;
  display: flex;
}

header span {
  font-size: 2rem;
}

.menu-profile-container {
  justify-content: space-evenly;
  align-items: center;
  display: flex;
}

.menu-profile-container div {
  text-align: center;
  color: #818181;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
}

.menu-profile-container div h2 {
  font-size: 1.25rem;
}

.menu-profile-container a {
  color: #818181;
  font-size: 1rem;
  text-decoration: none;
}

.side-nav {
  height: 100%;
  width: 0%;
  z-index: 1;
  background-color: #111;
  transition: all .5s;
  position: fixed;
  top: 0;
  right: 0;
  overflow-x: hidden;
}

#close-btn {
  color: #f1f1f1;
  padding: .5rem;
}

#user-list a, .side-nav .profile-options, .side-nav h4 {
  color: #818181;
  flex-direction: column;
  margin-top: 1rem;
  padding-left: .5rem;
  font-size: 1.25rem;
  text-decoration: none;
  display: flex;
}

.side-nav h4 {
  color: var(--txt-color);
  margin-top: 2.5rem;
  text-decoration: underline;
}

.side-nav a:hover {
  color: #f1f1f1;
}

.side-nav #close-btn {
  font-size: 2rem;
}

.profile-hero-section {
  width: 100%;
  background-color: #434343;
}

.profile-img {
  max-width: 12rem;
  width: 20%;
  aspect-ratio: 1;
  object-fit: cover;
  border-radius: 50%;
}

.write-post-section {
  width: 100%;
  padding: 1rem;
}

.posts-container {
  flex-direction: column-reverse;
  gap: 4rem;
  margin-top: 1rem;
  display: flex;
}

.post {
  background-color: #434343;
  flex-direction: column;
  display: flex;
}

.post-info {
  text-align: start;
  margin: 0 2rem;
  font-style: italic;
}

.post-message {
  text-align: start;
  margin-left: 2rem;
  font-size: 1rem;
}

.image-select {
  justify-content: center;
  align-items: center;
  display: flex;
}

.image-select img {
  max-width: 100%;
  width: clamp(5rem, 33.3333%, 15rem);
  filter: grayscale();
  border-radius: 50%;
  padding: 1rem;
}

.image-select img:hover {
  filter: grayscale(0%);
  transition: filter .5s;
}

.a-link {
  color: var(--txt-color);
  text-decoration: none;
}

#delete-account:hover {
  color: red;
}

